.work-order-card {
  background: white;
  border-radius: 12px;
  padding: 1.5rem;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
  border: 1px solid #e2e8f0;
  transition: all 0.2s ease;
  min-height: 200px;
  display: flex;
  flex-direction: column;
}
.work-order-card:hover {
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
  transform: translateY(-1px);
}
.work-order-card .card-header {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  margin-bottom: 1rem;
  flex-shrink: 0;
}
.work-order-card .card-header .title-section {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  flex: 1;
}
.work-order-card .card-header .title-section .icon {
  color: #6b7280;
  font-size: 1rem;
}
.work-order-card .card-header .title-section .title {
  font-size: 1.125rem;
  font-weight: 600;
  color: #1f2937;
  margin: 0;
  line-height: 1.4;
}
.work-order-card .card-header .badges {
  display: flex;
  gap: 0.5rem;
  flex-shrink: 0;
}
.work-order-card .card-header .badges .badge {
  padding: 0.25rem 0.75rem;
  border-radius: 20px;
  font-size: 0.75rem;
  font-weight: 500;
  text-transform: capitalize;
}
.work-order-card .card-header .badges .badge.status-open {
  background: #fef2f2;
  color: #dc2626;
}
.work-order-card .card-header .badges .badge.status-in-progress {
  background: #eff6ff;
  color: #2563eb;
}
.work-order-card .card-header .badges .badge.status-completed {
  background: #f0fdf4;
  color: #16a34a;
}
.work-order-card .card-header .badges .badge.status-cancelled {
  background: #f3f4f6;
  color: #6b7280;
}
.work-order-card .card-header .badges .badge.priority-low {
  background: #f0fdf4;
  color: #16a34a;
}
.work-order-card .card-header .badges .badge.priority-medium {
  background: #eff6ff;
  color: #2563eb;
}
.work-order-card .card-header .badges .badge.priority-high {
  background: #fef3c7;
  color: #d97706;
}
.work-order-card .card-header .badges .badge.priority-critical {
  background: #fef2f2;
  color: #dc2626;
}
.work-order-card .card-content {
  flex: 1;
  display: flex;
  flex-direction: column;
}
.work-order-card .card-content .description {
  color: #6b7280;
  font-size: 0.875rem;
  margin-bottom: 1rem;
  line-height: 1.5;
}
.work-order-card .card-content .details {
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
  flex: 1;
}
.work-order-card .card-content .details .detail-item {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  font-size: 0.875rem;
  color: #374151;
}
.work-order-card .card-content .details .detail-item .icon {
  color: #9ca3af;
  font-size: 0.75rem;
  width: 16px;
  text-align: center;
}
.work-order-card .card-content .details .detail-item .label {
  font-weight: 500;
  min-width: 60px;
}
.work-order-card .card-content .details .detail-item .value {
  color: #6b7280;
}
.work-order-card .card-footer {
  margin-top: auto;
  padding-top: 1rem;
  border-top: 1px solid #f3f4f6;
  flex-shrink: 0;
}
.work-order-card .card-footer .button-group {
  display: flex;
  gap: 0.5rem;
  flex-direction: column;
}
.work-order-card .card-footer .button-group .view-button,
.work-order-card .card-footer .button-group .edit-button {
  width: 100%;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  padding: 0.75rem 1rem;
  border: none;
  border-radius: 6px;
  font-size: 0.875rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s ease;
  text-decoration: none;
}
.work-order-card .card-footer .button-group .view-button svg,
.work-order-card .card-footer .button-group .edit-button svg {
  width: 1rem;
  height: 1rem;
}
.work-order-card .card-footer .button-group .view-button {
  background: #2563eb;
  color: white;
}
.work-order-card .card-footer .button-group .view-button:hover {
  background: #1d4ed8;
  transform: translateY(-1px);
}
.work-order-card .card-footer .button-group .edit-button {
  background: #f3f4f6;
  color: #374151;
  border: 1px solid #d1d5db;
}
.work-order-card .card-footer .button-group .edit-button:hover {
  background: #e5e7eb;
  border-color: #9ca3af;
  transform: translateY(-1px);
}

.list-view.work-order-card {
  display: flex;
  align-items: center;
  gap: 1.5rem;
  min-height: auto;
  padding: 1rem;
}
.list-view.work-order-card .card-header {
  margin-bottom: 0;
  flex: 1;
  min-width: 200px;
}
.list-view.work-order-card .card-content {
  flex: 2;
}
.list-view.work-order-card .card-content .details {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  gap: 0.75rem;
}
.list-view.work-order-card .card-footer {
  margin-top: 0;
  padding-top: 0;
  border-top: none;
  flex-shrink: 0;
  text-align: right;
}
.list-view.work-order-card .card-footer .view-button {
  width: auto;
  padding: 0.5rem 1rem;
}

@media (max-width: 768px) {
  .work-order-card {
    padding: 1rem;
    min-height: 180px;
  }
  .work-order-card .card-header {
    flex-direction: column;
    align-items: flex-start;
    gap: 0.75rem;
  }
  .work-order-card .card-header .badges {
    align-self: flex-start;
  }
  .work-order-card .card-content .details {
    gap: 0.5rem;
  }
  .work-order-card .card-footer .button-group {
    flex-direction: row;
    gap: 0.75rem;
  }
  .work-order-card .card-footer .button-group .view-button,
  .work-order-card .card-footer .button-group .edit-button {
    flex: 1;
    min-width: 0;
  }
  .list-view.work-order-card {
    flex-direction: column;
    align-items: flex-start;
    gap: 1rem;
    min-height: auto;
  }
  .list-view.work-order-card .card-content .details {
    grid-template-columns: 1fr;
  }
  .list-view.work-order-card .card-footer {
    text-align: left;
    width: 100%;
    border-top: 1px solid #f3f4f6;
    padding-top: 1rem;
    margin-top: 1rem;
  }
  .list-view.work-order-card .card-footer .button-group {
    flex-direction: row;
    gap: 0.75rem;
  }
  .list-view.work-order-card .card-footer .button-group .view-button,
  .list-view.work-order-card .card-footer .button-group .edit-button {
    flex: 1;
    min-width: 0;
  }
}
